:root {
  --main-border-color: rgb(230, 236, 240);
  --main-button-color: rgb(29, 161, 242);
  --secondary-font-color: rgb(101, 119, 134);
}

* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Ubuntu, 'Helvetica Neue', sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0px;
}

.application {
  display: flex;
  height: 100vh;
  justify-content: center;
  max-height: 100vh;
  max-height: -webkit-fill-available;
  overflow: hidden;
  width: 100vw;
}

.btn-primary {
  background-color: var(--main-button-color);
  border-color: var(--main-button-color);
}

.content {
  border-left: 1px solid var(--main-border-color);
  border-right: 1px solid var(--main-border-color);
  height: 100%;
  width: 600px;
}

.content > .owner {
  height: 0px;
  visibility: hidden;
}

.content__searchbar {
  height: 0px;
  visibility: hidden;
}

.main {
  display: flex;
  justify-content: space-between;
  height: 100%;
  width: fit-content;
}

.message {
  align-items: flex-start;
  border-bottom: 1px solid var(--main-border-color);
  display: flex;
  font-size: 15px;
  height: 80px;
  justify-content: space-between;
  padding: 15px;
  width: 100%;
}

.message__alias {
  margin-left: 4px;
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.message__author {
  align-items: center;
  display: flex;
  width: 243px;
}

.message__body {
  margin: 0px 8px;
}

.message__date {
  min-width: fit-content;
}

.message__icon {
  background-image: url('https://sandervreeken.github.io/Newco-Cakespace/images/SanderVreeken.jpg');
  background-position: center;
  background-size: contain;
  border-radius: 24.5px;
  height: 49px;
  min-width: 49px;
  width: 49px;
}

.message__name {
  font-weight: bold;
  width: fit-content;
}

.message__text {
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 243px;
}

.messenger {
  background-color: white;
  border-radius: 15px 15px 0px 0px;
  /* The bottom property has been set within the component itself to create an animation. */
  /* bottom: 0; */
  box-shadow: rgba(101, 119, 134, 0.2) 0px 0px 15px, rgba(101, 119, 134, 0.15) 0px 0px 3px 1px;
  height: 530px;
  margin-right: 64px;
  right: 0;
  position: fixed;
  width: 400px;
  z-index: 2;
}

.messenger__header {
  align-items: center;
  border-bottom: 1px solid var(--main-border-color);
  display: flex;
  height: 53px;
  justify-content: space-between;
  padding: 10px 15px;
  z-index: 1;
}

.messenger__icon {
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 9999px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  max-width: 34px;
  min-height: 34px;
  min-width: 34px;
  z-index: 3;
}

.messenger__icon > svg {
  height: 18px;
  min-width: 18px;
}

.messenger__title {
  margin: 0px 14px;
  text-align: left;
  width: 100%;
}

.navbar {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  padding: 0px 10px;
  width: 275px;
}

.navbar__button {
  align-items: center;
  background-color: var(--main-button-color);
  border-radius: 24.5px;
  color: white;
  display: flex;
  height: 49px;
  justify-content: center;
  margin: 15px 0px;
  width: 230px;
}

.navbar__button > svg {
  height: 26px;
  width: 0px;
  visibility: hidden;
}

.navbar__header {
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 24.5px;
  display: flex;
  height: 49px;
  justify-content: center;
  margin: 4px 0px 8px 0px;
  width: 49px;
}

.navbar__header > svg {
  height: 26px;
  width: 26px;
}

.navbar__icon {
  align-items: center;
  display: flex;
  height: 49px;
  justify-content: center;
  width: 49px;
}

.navbar__icon > svg {
  height: 26px;
  width: 26px;
}

.navbar__item {
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 9999px;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 6px;
  padding: 0px 24px 0px 12px
}

.navbar__main {
  height: fit-content                                                    ;
}

.owner {
  background-color: rgb(245, 248, 250);
  border-radius: 15px;
  height: fit-content;
  width: 100%;
}

.owner__check {
  align-items: center;
  display: flex;
  color: var(--secondary-font-color);
  display: flex;
  font-size: 13px;
  margin-bottom: 6px;
}

.owner__connect {
  align-items: center;
  display: flex;
  color: var(--main-button-color);
  padding: 10px 15px;
}

.owner__header {
  border-bottom: 1px solid var(--main-border-color);
  padding: 10px 15px;
}

.owner__icon {
  align-items: center;
  display: flex;
  height: 13px;
  margin-right: 8px;
  justify-content: center;
  width: 13px;
}

.owner__location {
  align-items: center;
  border-bottom: 1px solid var(--main-border-color);
  display: flex;
  font-size: 13px;
  padding: 10px 15px;
}

.owner__main {
  border-bottom: 1px solid var(--main-border-color);
  padding: 10px 15px;
}

.owner__message {
  font-size: 13px;
}

.owner__name {
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 4px;
}

.owner__validations {
  border-bottom: 1px solid var(--main-border-color);
  padding: 10px 15px;
}

.owner__since {
  color: var(--secondary-font-color);
  font-size: 13px;
}

.product {
  margin-top: 53px;
}

.product__buy {
  border-radius: 15px;
  font-size: 15px;
}

.product__description {
  font-size: 13px;
  padding: 10px 15px; 
}

.product__description > p {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.product__header {
  align-items: center;
  border-bottom: 1px solid var(--main-border-color); 
  display: flex;
  justify-content: space-between;
  padding: 10px 15px; 
}

.product__information {
  border-bottom: 1px solid var(--main-border-color); 
  border-top: 1px solid var(--main-border-color); 
  display: flex;
  font-size: 13px;
  height: fit-content;
  justify-content: space-around;
  padding: 10px 15px; 
  width: 100%;
}

.product__meta {
  align-items: center;
  border-bottom: 1px solid var(--main-border-color); 
  display: flex;
  font-size: 13px;
  justify-content: space-around;
  padding: 10px 15px; 
  width: 100%;
}

.product__metatag {
  align-items: center;
  display: flex;
}

.product__metatag > svg {
  margin-right: 6px;
}

.product__photo {
  background-image: url('https://sandervreeken.github.io/Newco-Cakespace/images/RhubarbPie.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 15px;
}

.product__photo:first-child {
  background-size: cover;
  grid-area: 1/1/3/2;
}

.product__photos {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  height: 284px;
  padding: 10px 15px; 
  width: 100%;
}

.product__stars {
  display: flex;
  flex-direction: row;
}

.product__title {
  width: 100%;
}

.searchbar {
  align-items: center;
  background-color: rgb(230, 236, 240);
  border-radius: 22px;
  display: flex;
  height: 44px;
  margin: 10px 0px;
  padding-right: 6px;
  width: 100%;
}

.searchbar__icon {
  align-items: center;
  display: flex;
  height: 44px;
  justify-content: center;
  width: 44px;
}

.searchbar__icon > svg {
  height: 18px;
  width: 18px; 
}

.searchbar__input {
  background-color: inherit;
  border: none;
  font-size: 15px;
  /* Width which has been adjusted by the difference in div and svg and padding from the right of the searchBar. */
  width: calc(100% - ((26px * 2) + 6px));
}

.searchbar__input {
  outline: none;
}

.sidebar {
  height: 100%;
  margin-left: 32px;
  width: 350px;
}

.user {
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 9999px;
  display: flex;
  justify-content: space-between;
  margin: 0px 0px 10px 0px;
  min-height: 39px;
  padding: 8px;
  width: 100%;
}

.user > svg {
  margin-right: 4px;
}

.user__body {
  align-items: center;
  display: flex;
}

.user__name {
  font-weight: bold;
}

.user__photo {
  background-image: url('https://sandervreeken.github.io/Newco-Cakespace/images/SanderVreeken.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 18.5px;
  height: 37px;
  width: 37px;
}

.user__profile {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  font-size: 15px;
  justify-content: center;
  margin: 0px 12px;
  text-align: left;
}

@media screen and (max-width: 1264px) {
  .navbar {
    width: 88px;
  }

  .navbar__add {
    visibility: hidden;
    width: 0px;
  }

  .navbar__button, .navbar__item {
    border-radius: 24.5px;
    height: 49px;
    width: 49px;
  }

  .navbar__button > svg {
    visibility: visible;
    width: 29px;
  }

  .navbar__name {
    visibility: hidden;
    width: 0px;
  }

  .user {
    border-radius: 24.5px;
    height: 49px;
    width: 49px;
  }

  .user__profile {
    visibility: hidden;
    width: 0px;
  }
}

@media screen and (max-width: 1082px) {
  .content__searchbar {
    padding: 10px 15px;  
    visibility: visible;
    width: 100%;
  }

  .messenger {
    visibility: hidden;
  }

  .content > .owner {
    border-bottom: 1px solid var(--main-border-color); 
    border-radius: 0px;
    border-top: 1px solid var(--main-border-color); 
    height: fit-content;
    visibility: visible;
  }
  
  .sidebar {
    margin-left: 0px;
    visibility: hidden;
    width: 0px;
  }
}

@media screen and (max-width: 698px) {
  .content {
    border: none;
    width: 100%;
  }

  .navbar {
    padding: 0px;
    visibility: hidden;
    width: 0px;
  }

  /* TODO: Check why the icon needs to be handled individually. */
  .navbar__button > svg {
    visibility: hidden;
    width: 0px;
  }
}

